___CSS_LOADER_EXPORT___.push([module.id, `.content {
    background-color: white;
}

.content > div {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
}

.add-task-btn-container {
    padding: 20px;
    display: flex;
    justify-content: center;
    min-width: 70%;
}

.add-task-btn {
    height: 40px;
    max-width: 70%;
    width: 100%;
    cursor: pointer;
    font-size: 1rem;
    font-weight: bold;
    background-color: var(--sub-color);
    color: var(--dark-main-color);
}

.add-task-btn:hover {
    background-color: rgb(231, 230, 227);
}

.add-task-btn,
.task-adder-container,
select,
.btn-container > button {
    border: 3px solid var(--main-color);
    border-radius: 10px;
}

.task-adder-container {
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 20px;
    color: var(--dark-main-color);
    font-weight: bold;
    max-width: fit-content;
    height: fit-content;
}

.task-adder-container > div {
    display: flex;
    gap: 10px;
    align-items: center;
}

.title-container > input,
.description-container > input {
    border-radius: 0;
    border: 0;
    border-bottom: 3px solid var(--main-color);
}

.required-container {
    background-color: var(--main-color);
    height: 30px;
    width: 90px;
    font-size: .9rem;
    font-weight: bold;
    border-radius: 5px;
    color: var(--sub-color);
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.drop-down-container,
.btn-container {
    justify-content: center;
}

select,
.btn-container > button {
    height: 40px;
    background-color: var(--sub-color);
    color: var(--dark-main-color);
    font-weight: bold;
    text-align: center;
}

button:hover {
    cursor: pointer;
}

select:focus {
    outline: none;
}

.header-container {
    display: flex;
    gap: 20px;
    align-items: center;
    margin-top: 18px;
}

.task-title > h3 {
    margin: 0;
}

.task-priority {
    font-size: 15px;
    font-weight: bold;
}

.task-priority p::before {
    content: ' ';
}

.task-priority p::after {
    content: ' ';
}

.task-container {
    background-color: var(--sub-color);
    border-radius: 10px;
    width: 90%;
    padding: 5px 20px;
    color: var(--dark-main-color);
    display: flex;
    align-items: center;
    gap: 20px;
    position: relative;
    overflow: hidden;
}

.task-project p::before {
    content: 'PROJECT : ';
    font-weight: bold;
}

.completed {
    text-decoration: line-through;
    text-decoration-thickness: 2px;
    background: repeating-linear-gradient(45deg,var(--sub-color),var(--sub-color) 10px,white 10px,white 20px);
}

.check-btn {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    border: 2px solid var(--dark-main-color);
}

.checked {
    background-color: var(--main-color);
    box-shadow:inset 0 0 0 2px white;
}

.task-container > div:nth-child(3) {
    margin-left: auto;
}

.user-input-container {
    max-width: 70%;
    width: 100%;
}

.icon-container {
    display: flex;
    gap: 10px;
}

.svg-icon-container {
    cursor: pointer;
}

svg {
    border: none;
    padding: 0;
    width: 30px;
}

path {
    fill: var(--dark-main-color);
}

.delete-btn {
    background-color: #f35454;
    color: white;
    padding: 8px 30px;
    border-radius: 10px 0 0 0;
    position: absolute;
    border: 1px solid #f35454;
    font-weight: bold;
    right: -125px;
    bottom: 0;
    transition: 1s;
}

.reveal {
    right: 0;
    bottom: 0;
}

.green {
    border-bottom: 6px solid var(--main-color);
}

.orange {
    border-bottom: 6px solid #ebc789;
}

.red {
    border-bottom: 6px solid #f97e7e;
}

.hide {
    display: none;
}

.search-hide {
    display: none;
}

@media screen and (max-width: 420px) {
    .required-container {
        display: none;
    }

    .task-adder-container > div,
    .header-container,
    .icon-container {
        flex-direction: column;
    }

    .task-adder-container > div:nth-child(-n + 2) > label::before {
        content: '*';
    }

    .header-container {
        align-items: flex-start;
        gap: 5px;
    }

    .task-container {
        min-height: 150px;
    }

}

