    box-sizing: border-box;
    height: 100%;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

:root {
    --main-color: rgb(143, 177, 143);
    --dark-main-color: rgb(98, 117, 98);
    --sub-color: rgb(240, 239, 236);
    font-family: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

body {
    margin: 0;
    height: 100%;
}

header {
    background-color: var(--main-color);
    min-height: 10%;
    display: flex;
}

.header-content-container {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.header-top-section {
    display: flex;
    background-color: var(--dark-main-color);
    justify-content: space-between;
}

.header-title-container {
    width: fit-content;
    padding: 5px 40px;
}

.header-title {
    font-size: 40px;
    font-weight: bold;
    color: white;
}

.input-container {
    display: flex;
    align-items: center;
    padding-right: 40px;
}

input {
    border-radius: 10px;
    border: 3px solid var(--main-color);
    min-height: 30px;
    text-indent: 5px;
}

input:focus {
    outline: 2px solid var(--sub-color);
}

.menu-btn {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.home-btn {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.search-btn {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.menu-btn:hover,
.home-btn:hover,
.add-btn:hover,
.search-btn:hover {
    cursor: pointer;
}

.icon-btn-container {
    display: flex;
    align-items: center;
}

.icon-btn {
    border: none;
    padding: 0;
    background-size: contain;
    background-repeat: no-repeat;
    width: 30px;
    height: 30px;
    filter: invert(100%) sepia(0%) saturate(7484%) hue-rotate(94deg) brightness(98%) contrast(103%);
}

.header-bottom-section > div:first-child {
    padding-right: 20px;
}

.header-bottom-section {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 5px 40px;
}

.visually-hidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@media screen and (max-width: 560px) {
    .input-container {
        display: none;
    }

    .header-top-section {
        justify-content: center;
    }
